<template>
    <div>{{ listItems }}</div>
    <div>title: {{ listItems.title }}</div>
</template>

<!-- <script setup>
const historyParams = history.params
console.log('history.state', history.params)
</script> -->

<script>
export default {
    // name: 'product',
    data: () => ({
        listItems: [],
    }),
    methods: {
        getActId() {
            return this.$route.params.id;
            // return history.state;
        },
        async getData(id) {
            console.log(`第${id}個產品`)
            // const res = await fetch(`https://jsonplaceholder.typicode.com/posts/${id}`);
            const res = await fetch(`https://btktech.com.tw/polyacetal/del/${id}`);
            const finalRes = await res.json();
            this.listItems = finalRes;
        },
    },
    mounted() {
        this.getData(this.getActId())
    }
};
</script>

